<template>
  <div id="create-character_background" class="create-character_background">
    <h2>Background</h2>
    <p>
      Backgrounds allow you to customize your character based on their life before adventuring.
This is the next step in their life story after their ancestry, which reflects the circumstances
of their birth. Your character’s background can help you learn or portray more about their
personality while also suggesting what sorts of things they’re likely to know. Consider
what events set your character on their path to the life of an adventurer and how those
circumstances relate to their background. 
    </p>

    <div class="select-background">
      <h3>Select a Background</h3>
      <div class="radio-toolbar select-background_list row">
        <template v-for="background in backgroundSelection">
          <div class="col-md-4 col-sm-6 col-6" :key="background.name + '-input'">
            <input
              type="radio"
              :id="'select-background_list-' + background.name"
              name="background"
              :value="background"
              v-model="selectedBackground"
            />
            <label
              :for="'select-background_list-' + background.name"
              class="select-background_list-label"
            >
              <div>
                <h4>{{ background.name }}</h4>
                <b>Ability: </b><span class="ability">{{background.ability[0]}}</span> or <span class="ability">{{background.ability[1]}}</span><br>
                <b>Skills: </b>{{Array.isArray(background.skill) ? "A choice of skill" : background.skill}} and {{Array.isArray(background.lore) ? "a choice of lore" : background.lore + " Lore"}}<br>
                <b>Feat: </b>{{Array.isArray(background.feat) ? "A choice of feat" :background.feat}}
              </div>
            </label>
          </div>
        </template>
      </div>
    </div>
    <div class="background-information information" v-if="selectedBackground != null">
      <h3>{{selectedBackground.name}}</h3>
      <p v-for="(p, i) of selectedBackground.backgroundText" :key="'background-text-' + i">{{p}}</p>
      <template v-if="Array.isArray(feat)">
        <div v-for="f in feat" :key="'feat-description' + f.name">
          <p><b>{{f.name}}</b><br>{{f.text}}</p>
        </div>
      </template>
      <template v-else>
        <p><b>{{feat.name}}</b><br>{{feat.text}}</p>
      </template>
    </div>
    <div class="background-abilities" v-if="selectedBackground != null">
      <h3>Select Abilities</h3>
      <p>Choose two ability boosts. One must be to <span class="ability">{{selectedBackground.ability[0]}}</span> or <span class="ability">{{selectedBackground.ability[1]}}</span>, and one is a free ability boost.</p>
      <div class="radio-toolbar select-background_list row">
          <template v-for="ability in selectedBackground.ability">
            <div class="col-md-2 col-sm-6 col-6" :key="'ability1-'+ability">
              <input
                type="radio"
                :id="'select-ability1-' + ability"
                name="ability1"
                :value="ability"
                v-model="firstBonus"
                :disabled = "secondBonus == ability"
              />
              <label
                :for="'select-ability1-' + ability"
                class="select-ability"
              >
                  {{character[ability]}}<br>
                  <b><span class="ability">{{ability}}</span></b>
              </label>
            </div>
          </template>
        </div>
        <div class="radio-toolbar select-background_list row">
          <template v-for="ability in abilities">
            <div class="col-md-2 col-sm-6 col-6" :key="'ability2-'+ability">
              <input
                type="radio"
                :id="'select-ability2-' + ability"
                name="ability2"
                :value="ability"
                v-model="secondBonus"
                :disabled = "firstBonus == ability"
              />
              <label
                :for="'select-ability2-' + ability"
                class="select-ability"
              >
                  {{character[ability]}}<br>
                  <b><span class="ability">{{ability}}</span></b>
              </label>
            </div>
          </template>
        </div>
    </div>
    <div class="background-skills" v-if="selectedBackground != null && Array.isArray(selectedBackground.feat)">
      <h3>Select Feat</h3>
      <p>Your background lets you have a choice of feat. Select one from the following list. </p>
        <div class="radio-toolbar select-lore_list row">
          <template v-for="f in feat">
            <div class="col-md-2 col-sm-6 col-6" :key="'feat-'+ f.name">
              <input
                type="radio"
                :id="'select-feat-' + f.name"
                name="feat"
                :value="f"
                v-model="selectedFeat"
              />
              <label
                :for="'select-feat-' + f.name"
                class="select-ability"
              >
                  <b><span class="ability">{{f.name}}</span></b>
              </label>
            </div>
          </template>
        </div>
    </div>
    <div class="background-skills" v-if="selectedBackground != null && Array.isArray(selectedBackground.skill)">
      <h3>Select Skill</h3>
      <p>Your background lets you have a choice of skill. Select one from the following list. </p>
        <div class="radio-toolbar select-lore_list row">
          <template v-for="skillName in selectedBackground.skill">
            <div class="col-md-2 col-sm-6 col-6" :key="'skill-'+skillName">
              <input
                type="radio"
                :id="'select-skill-' + skillName"
                name="skill"
                :value="skillName"
                v-model="skill"
              />
              <label
                :for="'select-skill-' + skillName"
                class="select-ability"
              >
                  <b><span class="ability">{{skillName}}</span></b>
              </label>
            </div>
          </template>
        </div>
    </div>
    <div class="background-skills" v-if="selectedBackground != null && Array.isArray(selectedBackground.lore)">
      <h3>Select Lore</h3>
      <p>Your background lets you have a choice of lore skill. Select one from the following list. </p>
        <div class="radio-toolbar select-lore_list row">
          <template v-for="loreName in selectedBackground.lore">
            <div class="col-md-2 col-sm-6 col-6" :key="'lore-'+loreName">
              <input
                type="radio"
                :id="'select-lore-' + loreName"
                name="lore"
                :value="loreName"
                v-model="lore"
              />
              <label
                :for="'select-lore-' + loreName"
                class="select-ability"
              >
                  <b><span class="ability">{{loreName}}</span></b>
              </label>
            </div>
          </template>
        </div>
    </div>
  </div>
</template>

<script>
import backgroundData from "../../data/backgrounds.json";
import featData from "../../data/feats.json";

export default {
  data: function() {
    return {
      backgroundData: backgroundData,
      featData: featData,
      selectedBackground: null,
      firstBonus: null,
      secondBonus: null,
      skill: null,
      lore: null,
      selectedFeat: null,
      abilities: ["strength", "dexterity", "constitution", "intelligence", "wisdom", "charisma"],
    };
  },
  watch: {
    selectedBackground: function(background) {
      this.$store.dispatch("characterCreator/setBackground", background);
      if (!Array.isArray(background.skill)) {
        this.skill = background.skill;
      }
      else {
        this.skill = null;
      }
      if (!Array.isArray(background.lore)) {
        this.lore = background.lore;
      }
      else {
        this.lore = null;
      }
      if (!Array.isArray(background.feat)) {
        this.selectedFeat = background.feat;
      }
      else {
        this.selectedFeat = null;
      }
    },
    firstBonus: function(bonus) {
      this.$store.dispatch("characterCreator/setBackgroundAbility1", bonus);
    },
    secondBonus: function(bonus) {
      this.$store.dispatch("characterCreator/setBackgroundAbility2", bonus);
    },
    skill: function(skill) {
      this.$store.dispatch("characterCreator/setBackgroundSkill", skill);
    },
    lore: function(lore) {
      this.$store.dispatch("characterCreator/setBackgroundLore", lore);
    },
    selectedFeat: function (feat) {
      this.$store.dispatch("characterCreator/setBackgroundFeat", feat);
    }
  },
  computed: {
    character() {
      return this.$store.state.characterCreator;
    },
    //The list of backgrounds a user can select
    backgroundSelection() {
      return this.backgroundData.backgrounds;
      // let allBackgrounds = this.backgroundData.backgrounds;
      // let filteredBackgrounds = [];
      // for (let background of allBackgrounds) {
      //   if (background.show){
      //     filteredBackgrounds.push(background);
      //   }
      // }
      // return filteredBackgrounds;
    },
    feat() {
      if (!this.selectedBackground){
        return {name:"", text:""};
      }

      if (Array.isArray(this.selectedBackground.feat)) {
        let feats = []
        for(let featOption of this.selectedBackground.feat) {
          for(let feat of this.featData.baseFeats.generalFeats){
            if (feat.name == featOption){
              feats.push(feat);
            }
          }
        }
        return feats;
      }

      //Only one feat
      else {
        for(let feat of this.featData.baseFeats.generalFeats){
          if (feat.name == this.selectedBackground.feat){
            this.$store.dispatch("characterCreator/setBackgroundFeat", feat);
            return feat;
          }
        }
      }
      
      return {name:"", text:""};
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
