<template>
  <div class="container">
    <h1>Create a Character</h1>
    <template>
  <div>
    <v-stepper alt-labels :value="character.step">
      <v-stepper-header>
        <v-stepper-step step="1">
          Ancestry
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step step="2">
          Background
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step step="3">
          Class
        </v-stepper-step>

        <v-divider></v-divider>
        <v-stepper-step step="4">
          Skills
        </v-stepper-step>

        <v-divider></v-divider>
        <v-stepper-step step="5">
          Feats
        </v-stepper-step>

        <v-divider></v-divider>
        <v-stepper-step step="6">
          Equipment
        </v-stepper-step>

        <v-divider></v-divider>
        <v-stepper-step step="7">
          Spells
        </v-stepper-step>

        <v-divider></v-divider>
        <v-stepper-step step="8">
          Details
        </v-stepper-step>
      </v-stepper-header>
    </v-stepper>
    
  </div>
</template>
    <SelectAncestry v-if="character.step == 1"></SelectAncestry>
    <SelectBackground v-if="character.step == 2"></SelectBackground>
    <SelectClass v-if="character.step == 3"></SelectClass>
    <SelectSkills v-if="character.step == 4"></SelectSkills>

    <div class="step-buttons">
    <button @click="nextStep" class="btn btn-primary">Next Step</button>
    <button @click="previousStep" class="btn btn-primary" v-if="character.step != 1">Previous Step</button>
    </div>
    <div id="character-debug">
      <b>Character Debug Info</b>
      <div v-for="(value, key) in character" :key="key">
        {{key }}:{{ value }}
      </div>
    </div>
    <div>Special thanks to Jim Barnes for his work converting Pathfinder 2e data into json.</div>
  </div>
</template>

<script>
import SelectAncestry from "../components/character-creator/SelectAncestry";
import SelectBackground from "../components/character-creator/SelectBackground";
import SelectClass from "../components/character-creator/SelectClass";
import SelectSkills from "../components/character-creator/SelectSkills";

export default {
  name: "CharacterCreator",
  components: {
    SelectAncestry,
    SelectBackground,
    SelectClass,
    SelectSkills
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    character() {
      return this.$store.state.characterCreator;
    },
  },
  methods: {
    nextStep: function() {
      this.$store.dispatch("characterCreator/nextStep");
    },
    previousStep: function() {
      this.$store.dispatch("characterCreator/previousStep");
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push("/login");
    }
  },
};
</script>

<style lang="scss">
.step-buttons {
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse
}
</style>
