<template>
  <div id="create-character_ancestry" class="create-character_ancestry">
    <h2>Ancestry</h2>
    <p>
      Your character’s ancestry determines which people they call their own, whether it’s diverse
      and ambitious humans, insular but vivacious elves, traditionalist and family-focused
      dwarves, or any of the other folk who call Golarion home. A character’s ancestry and their
      experiences prior to their life as an adventurer—represented by a background—might be
      key parts of their identity, shape how they see the world, and help them find their place in it.
    </p>

    <div class="select-ancestry">
      <h3>Select an Ancestry</h3>
      <div class="radio-toolbar select-ancestry_list row">
        <template v-for="(ancestry, ancestryName) in ancestrySelection">
          <div class="col-md-2 col-sm-4 col-6" :key="ancestryName + '-input'">
            <input
              :key="ancestryName + '-input'"
              type="radio"
              :id="'select-ancestry_list-' + ancestryName"
              name="ancestry"
              :value="ancestry"
              v-model="selectedAncestry"
            />
            <label
              :for="'select-ancestry_list-' + ancestryName"
              :key="ancestryName + '-label'"
              class="select-ancestry_list-label"
            >
              <div>
                <img class="select-ancestry_list-image" :src="require('@/assets/images/ancestry/' + ancestry.image)">
                <span>{{ ancestry.name }}</span>
              </div>
            </label>
          </div>
        </template>
      </div>
    </div>

    <div class="ancestry-description information" v-if="selectedAncestry">
    <h3>{{selectedAncestry.name}}</h3>
    <p v-for="(p, i) in selectedAncestry.description" :key="i">{{p}}</p>
    </div>

    <div class="ancestry-features" v-if="selectedAncestry && ancestryFeatures.length > 0">
      <h3>{{selectedAncestry.name}} Features</h3>
      <div>
        <b>Hit Points:</b> {{selectedAncestry.hp}}
      </div>
      <div >
        <b>Speed:</b> {{selectedAncestry.speed}}
      </div>
      <div>
        <b>Size:</b> {{selectedAncestry.size}}
      </div>
      <div v-for="feature in ancestryFeatures" :key="feature.name">
        <b>{{feature.name}}: </b>
        <span>{{feature.description}}</span>
      </div><div>
        <b>Languages:</b> <span v-for="language of selectedAncestry.languages" :key="language" class="language">{{language}}</span>
      </div>
    </div>

    <div class="assign-abilities" v-if="selectedAncestry">
      <h3>Assign Ability Points</h3>
      <p>{{selectedAncestry.abilityDescription}}</p>
      <p>Select {{selectedAncestry.freeBoosts}} {{selectedAncestry.freeBoosts > 1 ? "abilities" : "ability"}} to boost.</p>
      <div class="checkbox-toolbar assign-abilities row">
        <template v-for="ability in abilities">
        <div class="col-md-2 col-sm-4 col-6" :key="ability + '-boost'">
          <input
            :key="ability + '-boost-input'"
            type="checkbox"
            :id="'assign-abilities-' + ability"
            name="ancestry"
            :disabled="(boostedAbilities.length >= selectedAncestry.freeBoosts 
              && boostedAbilities.indexOf( ability ) === -1)
              || selectedAncestry.abilityBoosts.indexOf(ability) !== -1"
            :value="ability"
            v-model="boostedAbilities"
          />
          <label
            :for="'assign-abilities-' + ability"
            :key="ability + '-boost-label'"
            class="assign-abilities-label"
          >{{ character[ability] }}<br>
          <span>{{ ability }}</span></label>
        </div>
      </template>
      </div>
    </div>

    <div class="select-heritage" v-if="selectedAncestry">
      <h3>Select {{selectedAncestry.name == "Elf"? "an" : "a"}} {{selectedAncestry.name}} Heritage</h3>
      <div class="select-heritage radio-toolbar row">
        <div v-for="heritage in heritageSelection" :key="'heritage-' + heritage.heritageName" class="col-12">
          <input class="select-heritage_input"
              type="radio"
              :id="'select-heritage-' + heritage.heritageName"
              name="heritage"
              :value="heritage"
              v-model="selectedHeritage">
          <label
            :for="'select-heritage-' + heritage.heritageName"
            class="select-heritage_label"
          >
            <h4>{{heritage.heritageName}}</h4>
            <p>{{heritage.desc}}</p>
            </label>
        </div>
      </div>
    </div>

    <div class="select-ancestry-feat" v-if="selectedAncestry">
      <h3>Select {{selectedAncestry.name == "Elf"? "an" : "a"}} {{selectedAncestry.name}} Feat</h3>
      <div class="select-ancestry-feat radio-toolbar row">
        <div v-for="feat in ancestryFeatsSelection" :key="'ancestry-feat-' + feat.name" class="col-md-6 col-12">
          <input class="select-ancestry-feat_input"
              type="radio"
              :id="'select-ancestry-feat-' + feat.name"
              name="ancestryFeat"
              :value="feat"
              v-model="ancestryFeat">
          <label
            :for="'select-ancestry-feat-' + feat.name"
            class="select-ancestry-feat_label"
          >
            <h4>{{feat.name}}</h4>
            <p v-if="!ancestryFeat || ancestryFeat.name != feat.name">{{feat.benefits}}</p>
            <div v-else><p v-for="(text, i) of feat.text" :key="'selected-feat-text'+i">{{text}}</p></div>
            </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ancestryData from "../../data/ancestry.json";
import heritageData from "../../data/heritage.json";
import ancestryFeatsData from "../../data/ancestry-feats.json";

export default {
  data: function() {
    return {
      selectedAncestry: null,
      selectedHeritage: null,
      ancestryData: ancestryData,
      heritageData: heritageData,
      ancestryFeat: null,
      ancestryFeatsData: ancestryFeatsData,
      abilities: ["strength", "dexterity", "constitution", "intelligence", "wisdom", "charisma"],
      boostedAbilities: []
    };
  },
  watch: {
    selectedAncestry: function(ancestry) {
      this.boostedAbilities = [];
      this.selectedHeritage = null;
      this.ancestryFeat = null;
      this.$store.dispatch("characterCreator/setAncestry", ancestry);
    },
    selectedHeritage: function(heritage) {
      if (!heritage){
        return;
      }
      this.boostedAbilities = [];
      if (heritage.name == "Half-Elf" || heritage.name == "Half-Orc") {
        this.ancestryFeat = null;
      }
      this.$store.dispatch("characterCreator/setHeritage", heritage);
    },
    boostedAbilities: function(abilities) {
      this.$store.dispatch("characterCreator/setAncestryBoostedAbilities", abilities);
    },
    ancestryFeat: function(feat) {
      this.$store.dispatch("characterCreator/setAncestryFeat1", feat);
    }
  },
  computed: {
    character() {
      return this.$store.state.characterCreator;
    },

    //The list of ancestries a user can select
    ancestrySelection() {
      let allAncestries = this.ancestryData.ancestries;
      let filteredAncestries = {};
      for (let a in allAncestries) {
        if (allAncestries[a].displayInSelection){
          filteredAncestries[a] = allAncestries[a]
        }
      }
      return filteredAncestries;
    },

    //The list of heritages a user can select
    heritageSelection() {
      if (this.selectedAncestry == null)
        return [];

      let allHeritages = this.heritageData.heritages;
      let filteredHeritages = [];
      for (let heritage of allHeritages) {
        if (heritage.ancestryName === this.selectedAncestry.name
            && heritage.source.includes("Core Rulebook")) {
          filteredHeritages.push(heritage)
        }
      }
      return filteredHeritages;
    },

    ancestryFeatsSelection() {
      if (this.selectedAncestry == null)
        return [];

      let ancestryFeats = this.ancestryFeatsData[this.selectedAncestry.name];

      if (this.selectedHeritage && this.selectedHeritage.heritageName == "Half-Elf"){
        ancestryFeats = this.ancestryFeatsData["Half-Elf"];
      }
      if (this.selectedHeritage && this.selectedHeritage.heritageName == "Half-Orc"){
        ancestryFeats = this.ancestryFeatsData["Half-Orc"];
      }
      
      let filteredFeats = [];
      for (let feat of ancestryFeats) {
        if (feat.level == "1"){
          filteredFeats.push(feat)
        }
      }
      return filteredFeats;
    },

    //The features belonging to the selected ancestry
    ancestryFeatures() {
      if (this.selectedAncestry == null)
        return [];

      let featureNames = this.selectedAncestry.features;
      let allFeatures = ancestryData.features;
      let ancestryFeatures = [];
      for (let feature of allFeatures) {
        if (featureNames.includes(feature.name)) {
          ancestryFeatures.push(feature);
        }
      }
      
      this.$store.dispatch("characterCreator/setAncestryFeatures", ancestryFeatures);
      return ancestryFeatures;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
#app {
  #create-character_ancestry {
    .assign-abilities,
    .select-ancestry_list,
    {
      justify-content: left;
      align-items: stretch;

      &-label {
        margin: 0.8rem 0 0.8rem 0;
        width: 100%;
        text-align: center;

        span{ 
          font-weight: bold;
          text-transform: capitalize;
        }
      }

      &-image {
        width: 100%;
      }
    }

    .select-ancestry-feat_label,
    .select-heritage_label {
      text-align: left;
      width:100%;
    }
    

    span.language {
      &:not(:last-child):after{
          content: ', '
      }
      &:last-child:before{
          content: 'and '
      }
      &:last-child:after{
          content: '.'
      }
    }
  }
}
</style>
