        width: 100%;
      }
    }

    .select-skills-row:nth-child(even){
      background: #f8f8f8;
    }

    td.ability {
      text-transform: capitalize;
    }
    td.skill-name {
      text-align: left;
      padding: 5px 10px;
    }
    td.skill-total {
      text-align: right;
      width: 20px;
      padding: 5px 10%;
    }
    td.ability-name {
      text-align: center;
      width: 20px;
      padding: 5px 15px 5px 10%;
    }
    td.ability-mod {
      text-align: right;
      width: 20px;
      padding: 5px 10% 5px 15px;
    }
    td.prof-check-box {
      text-align: center;
      width: 20px;
      padding: 5px 15px 5px 10%;
    }
    td.prof-bonus {
      text-align: right;
      width: 20px;
      padding: 5px 10% 5px 15px;
    }
  }
}
</style>
