<template>
  <div id="create-character_class" class="create-character_class">
    <h2>Class</h2>
    <p>
      Just as your character’s ancestry plays a key role in expressing their identity and worldview,
their class indicates the training they have and will improve upon as an adventurer.
Choosing your character’s class is perhaps the most important decision you will make
for them. Groups of players often create characters whose skills and abilities complement
each other mechanically—for example, ensuring your party includes a healer, a combatoriented character, a stealthy character, and someone with command over magic—so you
may wish to discuss options with your group before deciding. 
    </p>

    <div class="select-class">
      <h3>Select a Class</h3>
      <div class="radio-toolbar select-class_list row">
        <template v-for="klass in classData">
          <div class="col-md-2 col-sm-4 col-6" :key="klass.name + '-input'">
            <input
              type="radio"
              :id="'select-class_list-' + klass.name"
              name="klass"
              :value="klass"
              v-model="selectedClass"
            />
            <label
              :for="'select-class_list-' + klass.name"
              class="select-class_list-label"
            >
              <div>
                <h4>{{ klass.name }}</h4>
              </div>
            </label>
          </div>
        </template>
      </div>
    </div>
    
    <div class="class-information information" v-if="selectedClass != null">
      <h3>{{selectedClass.name}}</h3>
      <h4>Key Ability</h4>
      <b><span class="ability">{{selectedClass.key_ability_choices[0]}}</span><template v-if="selectedClass.key_ability_choices[1]"> or <span class="ability">{{selectedClass.key_ability_choices[1]}}</span></template></b>
      <p>At 1st level, your class gives you a bonus to <span class="ability">{{selectedClass.key_ability_choices[0]}}</span><template v-if="selectedClass.key_ability_choices[1]"> or <span class="ability">{{selectedClass.key_ability_choices[1]}}</span></template>.</p>
       <h4>Hit Points</h4>
      <b>{{selectedClass.hp + calcMod(character.constitution)}}</b>
      <p>At 1st level and every level thereafter you increase your hitpoints by {{selectedClass.hp}} plus your Constitution modifier.</p>
    
    </div>
  </div>
</template>

<script>
import classData from "../../data/class.json";
import featData from "../../data/feats.json";

export default {
  data: function() {
    return {
      classData: classData,
      featData: featData,
      selectedClass: null,
      classFeat: null,
      abilities: ["strength", "dexterity", "constitution", "intelligence", "wisdom", "charisma"],
    };
  },
  watch: {
    selectedClass: function(klass) {
      this.$store.dispatch("characterCreator/setClass", klass);
    }
  },
  methods: {
    calcMod(ability) {
      return Math.floor((ability - 10) / 2);
    }
  },
  computed: {
    character() {
      return this.$store.state.characterCreator;
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
